import React, { useState } from 'react';
import config from './config';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import './components/Navbar.css';
import './App.css';
import {Link, Route, Routes} from "react-router-dom";
import WeeklyForecast from './components/WeeklyForecast';

const WeatherApp = () => {
    const [city, setCity] = useState('');
    const [weatherData, setWeatherData] = useState(null);
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);
    const [showAbout, setShowAbout] = useState(false);

    const API_KEY = config.API_KEY;

    const handleCityChange = (e) => {
        const cityInput = e.target.value;
        const formattedCity = cityInput.charAt(0).toLocaleUpperCase('tr-TR') + cityInput.slice(1).toLocaleLowerCase('tr-TR');
        setCity(formattedCity);
    };


    // Arka plan resimlerinin tanmlanmas
    const backgroundImages = {
        // Kar ve karla ilgili durumlar
        "kar fleme": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "srklenen kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar saanaklar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "youn kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar ve yamur saanaklar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif yamur ve kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "iddetli yamur ve kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',

        // iseleme
        "iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif iseleme/yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli iseleme/yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',

        // Yamur
        "yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "youn ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "saanak yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "yamur ve kar": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',

        // Sis ve duman
        "sis": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "duman veya pus": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "dondurucu sis": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',

        // Frtna ve kasrga
        "frtna": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "frtnalar": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "huni bulutu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "kasrga": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',

        // Dier hava koullar
        "toz frtnas": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "bulutlu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "paral bulutlu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "ak": 'url("/weatherapp/weather-backgrounds/sunny.jpg")',
        "yldrmsz yldrm": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "youn dondurucu iseleme/dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif dondurucu iseleme/dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "iddetli dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "evrede ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "buz": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "elmas tozu": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "dolu": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "gkyz kapsam azalan": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "gkyz kapsam artan": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "gkyz deimedi": 'url("/weatherapp/weather-backgrounds/sunny.jpg")',

        "default": 'url("/weatherapp/weather-backgrounds/default.jpg")',
    };

    // Hava durumu kouluna gre arka plan seimi
    const getBackground = (condition) => {
        if (!condition) return backgroundImages["default"];

        condition = condition.toLowerCase();
        if (condition.includes('ak') || condition.includes('gneli')) return backgroundImages["ak"];
        if (condition.includes('kar')) return backgroundImages["kar"];
        if (condition.includes('yamur')) return backgroundImages["yamur"];
        if (condition.includes('sis') || condition.includes('pus')) return backgroundImages["sis"];
        if (condition.includes('frtna') || condition.includes('kasrga')) return backgroundImages["frtna"];
        if (condition.includes('toz frtnas')) return backgroundImages["toz frtnas"];
        if (condition.includes('bulut')) return backgroundImages["bulutlu"];
        return backgroundImages["default"];
    };

    // Hava durumu verisini API'den ekme
    const getWeather = async () => {
        if (!city.trim()) {
            setError('Ltfen bir ehir ad girin');
            return;
        }

        setLoading(true);
        setError('');

        try {
            const response = await fetch(
                `https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/${city}?unitGroup=metric&key=${API_KEY}&contentType=json&lang=tr`
            );

            if (!response.ok) {
                throw new Error('ehir bulunamad');
            }

            const data = await response.json();
            setWeatherData(data);
        } catch (err) {
            setError('Hava durumu bilgisi alnamad. Ltfen geerli bir ehir ad girin.');
        } finally {
            setLoading(false);
        }
    };

    // 'Enter' tuuna basldnda hava durumu verisini al
    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            getWeather();
        }
    };

    // Inside your component, just before return:
    console.log("Current background:", weatherData?.currentConditions?.conditions);
    console.log("Selected background:", weatherData ? getBackground(weatherData.currentConditions?.conditions) : backgroundImages.default);

    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/weatherapp">WeatherApp</Link>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarNav"
                        aria-controls="navbarNav"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link className="nav-link active" to="/weatherapp">Ana Sayfa</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/haftalik">Haftalk Tahmin</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>

            <Routes>
                <Route path="/weatherapp" element={
                    <div
                        className="min-vh-100 d-flex justify-content-center align-items-center py-5"
                        style={{
                            backgroundImage: weatherData
                                ? getBackground(weatherData.currentConditions?.conditions)
                                : backgroundImages.default,
                            backgroundSize: 'cover',
                            backgroundPosition: 'center',
                            backgroundRepeat: 'no-repeat',
                            transition: 'background-image 0.5s ease'
                        }}
                    >
                        <div className="container py-2">
                            <div className="row justify-content-center">
                                <div className="col-12 col-md-8 col-lg-6">
                                    <div className="card shadow-lg"
                                         style={{backgroundColor: 'rgba(255, 255, 255, 0.9)'}}>
                                        <div className="card-body">
                                            <h1 className="text-center mb-4 fs-4 fs-sm-3">Hava Durumu</h1>

                                            <div className="mb-4">
                                                <div className="input-group">
                                                    <input
                                                        type="text"
                                                        className="form-control"
                                                        placeholder="ehir ad girin..."
                                                        value={city}
                                                        onChange={handleCityChange}
                                                        onKeyPress={handleKeyPress}
                                                    />
                                                    <button
                                                        className="btn btn-primary"
                                                        onClick={getWeather}
                                                        disabled={loading}
                                                    >
                                                        {loading ? 'Ykleniyor...' : 'Ara'}
                                                    </button>
                                                </div>
                                                {error && <div className="text-danger mt-2 text-center">{error}</div>}
                                            </div>

                                            {weatherData && (
                                                <div className="weather-info">
                                                    <h2 className="text-center mb-3">{weatherData.address}</h2>
                                                    <div className="row text-center">
                                                        <div className="col-6 col-md-4 mb-3">
                                                            <div className="card h-100">
                                                                <div className="card-body">
                                                                    <h5>Scaklk</h5>
                                                                    <p className="display-6">{weatherData.currentConditions.temp}C</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="col-6 col-md-4 mb-3">
                                                            <div className="card h-100">
                                                                <div className="card-body">
                                                                    <h5>Nem</h5>
                                                                    <p className="display-6"> %{weatherData.currentConditions.humidity}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="col-6 col-md-4 mb-3">
                                                            <div className="card h-100">
                                                                <div className="card-body">
                                                                    <h5>Rzgar Hz</h5>
                                                                    <p className="display-6">{weatherData.currentConditions.windspeed} km/s</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="col-6 col-md-12 mb-3">
                                                            <div className="card h-100 text-center">
                                                                <div className="card-body">
                                                                    <h5>Hava Olay</h5>
                                                                    <p className="display-6">{weatherData.currentConditions.conditions}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}

                                            <div className="text-center mt-4">
                                                <button
                                                    className="btn btn-info"
                                                    onClick={() => setShowAbout(!showAbout)}
                                                >
                                                    Hakknda
                                                </button>
                                            </div>

                                            {showAbout && (
                                                <div className="mt-3 text-center">
                                                    <div className="card">
                                                        <div className="card-body">
                                                            <h5>Gelitirici</h5>
                                                            <p>sim: Semih Kartal</p>
                                                            <a href="https://github.com/zhentilar/">GitHub:
                                                                zhentilar</a>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                }/>
                <Route path="/haftalik" element={<WeeklyForecast city={city}/>}/>
            </Routes>
        </div>
    );
};

export default WeatherApp;
