import React, {useState} from 'react';
import config from '../config';

const WeeklyForecast = ({ city, background }) => {
    const [forecast, setForecast] = React.useState(null);
    const [error, setError] = React.useState('');
    const [loading, setLoading] = React.useState(false);
    const [weatherData, setWeatherData] = useState(null);

    React.useEffect(() => {
        console.log("City in WeeklyForecast:", city); // Debug city prop
        if (city) {
            getForecast();
        }
    }, [city]);

    const getForecast = async () => {
        if (!city) {
            setError('ehir seilmedi');
            return;
        }

        setLoading(true);
        setError('');
        console.log("Fetching forecast for:", city); // Debug API call

        try {
            const response = await fetch(
                `https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/${city}?unitGroup=metric&key=${config.API_KEY}&contentType=json&lang=tr`
            );

            if (!response.ok) {
                throw new Error('ehir bulunamad');
            }

            const data = await response.json();
            console.log("Forecast data:", data); // Debug API response
            setForecast(data.days.slice(0, 7));
            setWeatherData(data);
        } catch (err) {
            console.error("Forecast error:", err); // Debug errors
            setError('Hava durumu tahmini alnamad.');
        } finally {
            setLoading(false);
        }
    };

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        return date.toLocaleDateString('tr-TR', { weekday: 'long', month: 'long', day: 'numeric' });
    };

    // Arka plan resimlerinin tanmlanmas
    const backgroundImages = {
        // Kar ve karla ilgili durumlar
        "kar fleme": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "srklenen kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar saanaklar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "youn kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "kar ve yamur saanaklar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif yamur ve kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "iddetli yamur ve kar": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',

        // iseleme
        "iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif iseleme": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif iseleme/yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli iseleme/yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',

        // Yamur
        "yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "iddetli yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "youn ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "saanak yamur": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "hafif ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "yamur ve kar": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',

        // Sis ve duman
        "sis": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "duman veya pus": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "dondurucu sis": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',

        // Frtna ve kasrga
        "frtna": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "frtnalar": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "huni bulutu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "kasrga": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',

        // Dier hava koullar
        "toz frtnas": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "bulutlu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "paral bulutlu": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "ak": 'url("/weatherapp/weather-backgrounds/sunny.jpg")',
        "yldrmsz yldrm": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "youn dondurucu iseleme/dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif dondurucu iseleme/dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "iddetli dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "hafif dondurucu yamur": 'url("/weatherapp/weather-backgrounds/snowy.jpg")',
        "evrede ya": 'url("/weatherapp/weather-backgrounds/rainy.jpg")',
        "buz": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "elmas tozu": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "dolu": 'url("/weatherapp/weather-backgrounds/default.jpg")',
        "gkyz kapsam azalan": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "gkyz kapsam artan": 'url("/weatherapp/weather-backgrounds/cloudy.jpg")',
        "gkyz deimedi": 'url("/weatherapp/weather-backgrounds/sunny.jpg")',
    };

    // Hava durumu kouluna gre arka plan seimi
    const getBackground = (condition) => {
        if (!condition) return backgroundImages["default"];

        condition = condition.toLowerCase();
        if (condition.includes('ak') || condition.includes('gneli')) return backgroundImages["ak"];
        if (condition.includes('kar')) return backgroundImages["kar"];
        if (condition.includes('yamur')) return backgroundImages["yamur"];
        if (condition.includes('sis') || condition.includes('pus')) return backgroundImages["sis"];
        if (condition.includes('frtna') || condition.includes('kasrga')) return backgroundImages["frtna"];
        if (condition.includes('toz frtnas')) return backgroundImages["toz frtnas"];
        if (condition.includes('bulut')) return backgroundImages["bulutlu"];
        return backgroundImages["default"];
    };

    // Debug render state
    console.log("Background Prop:", background);

    // Always render something, even if just loading or error states
    return (
        <div className="min-vh-100 bg-light py-5" style={{
            backgroundImage: weatherData
                ? getBackground(weatherData.currentConditions?.conditions)
                : backgroundImages.default,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
            transition: 'background-image 0.5s ease'
        }}>
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-md-10">
                        <div className="card shadow" style={{backgroundColor: 'rgba(255, 255, 255, 0.9)'}}>
                            <div className="card-body">
                                <div className="d-flex justify-content-between align-items-center mb-4">
                                    <h2 className="mb-0">Haftalk Hava Durumu {city ? `- ${city}` : ''}</h2>

                                </div>

                                {loading && <div className="text-center">Ykleniyor...</div>}
                                {error && <div className="alert alert-danger">{error}</div>}
                                {!city && <div className="alert alert-info">Ltfen ana sayfadan bir ehir sein.</div>}

                                {forecast && forecast.length > 0 ? (
                                    <div className="row">
                                        {forecast.map((day, index) => (
                                            <div key={index} className="col-md-6 col-lg-3 mb-4">
                                                <div className="card h-100">
                                                    <div className="card-body">
                                                        <h5 className="card-title">{formatDate(day.datetime)}</h5>
                                                        <div className="my-3">
                                                            <p className="mb-2">
                                                                <strong>Scaklk:</strong> {day.temp}C
                                                            </p>
                                                            <p className="mb-2">
                                                                <strong>En Yksek:</strong> {day.tempmax}C
                                                            </p>
                                                            <p className="mb-2">
                                                                <strong>En Dk:</strong> {day.tempmin}C
                                                            </p>
                                                            <p className="mb-2">
                                                                <strong>Nem:</strong> %{day.humidity}
                                                            </p>
                                                            <p className="mb-0">
                                                                <strong>Durum:</strong> {day.conditions}
                                                            </p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                ) : null}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default WeeklyForecast;